import MasakanList from '../views/pages/masakan-list';
import Reviewers from '../views/pages/our-reviewer';
import Favorite from '../views/pages/favorite';
import Detail from '../views/pages/masakan-detail';
import About from '../views/pages/about';

// Di dalamnya kita tuliskan URL dan halaman yang ingin dimunculkan pada url tersebut.
const routes = {
    '/': MasakanList, // default page
    '/home': MasakanList,
    '/partners': MasakanList,
    '/our-reviewers': Reviewers,
    '/my-favorite': Favorite,
    '/about': About,
    '/detail/:id': Detail,
};

export default routes;
